import Decode from './Decode';
import Webaudio from './Webaudio';
import { EventEmitter } from 'events';

export interface IFormat {
  formatID: string,
  sampleRate: number,
  channelsPerFrame: number,
  floatingPoint: boolean,
}

export default class Player extends EventEmitter{
  private webaudio: Webaudio;
  private decode: Decode;
  private queue: Array<Float32Array>;
  private cacheFrame: Float32Array;
  private cacheFrameOffset: number;
  private format: IFormat = {
    formatID: 'opus',
    sampleRate: 48000,
    channelsPerFrame: 2,
    floatingPoint: true
  }

  constructor() {
    super();
    this.queue = [];
    this.decode = new Decode(this.format);
  }

  play() {
    this.webaudio = new Webaudio(this.format);
    this.webaudio.on('refill', this.onRiff.bind(this));
  }

  onRiff(buffer: AudioBuffer) {
    // console.log("onRiff");
    let bufferOffset = 0;
    if (!this.cacheFrame) {
      this.cacheFrame = this.queue.shift();
      this.cacheFrameOffset = 0;
    }
    while (this.cacheFrame && bufferOffset < buffer.length) {
      let max = Math.min(this.cacheFrame.length - this.cacheFrameOffset, buffer.length - bufferOffset);
      for (let i = 0; i < max; i++) {
        // @ts-ignore
        buffer[bufferOffset++] = this.cacheFrame[this.cacheFrameOffset++];
      }
      if (this.cacheFrameOffset === this.cacheFrame.length) {
        this.cacheFrame = this.queue.shift();
        this.cacheFrameOffset = 0;
      }
    }
    this.queue = [];
  }

  readPacket(packet: Uint8Array) {
    try {
      let decodedPacket = this.decode.decodePacket(packet);
      this.queue.push(decodedPacket);
    } catch (error) {
      this.emit('error', error);
    }
  }
  
}